<template>
   <div class="BXviewcoach__BXvidschedule__BxSchedule">
      <div class="HeadTitle">
         <p class="title">Coach Availability</p>
         <span>You will be Available for total <b>70 Hours </b>/ Week</span>
      </div>
      <div class="contSchedule" style="min-height:290px;max-height:290px;overflow:scroll ">
         <div class="BxinfoSchedule">
            <div class="bxlabel">
               <p>Monday</p>
            </div>
            <div class="bxListTime">
              <div class="times" 
               v-for="(n, i) in dataMonday"
               :key="i"
              >
               {{n.start}} - {{n.end}}
              </div>
            </div>
         </div>
         <div class="BxinfoSchedule">
            <div class="bxlabel">
               <p>Tuesday</p>
            </div>
            <div class="bxListTime">
              <div class="times" 
               v-for="(n, i) in dataTuesday"
               :key="i"
              >
               {{n.start}} - {{n.end}}
              </div>
            </div>
         </div>
         <div class="BxinfoSchedule">
            <div class="bxlabel">
               <p>Wednesday</p>
            </div>
            <div class="bxListTime">
              <div class="times" 
               v-for="(n, i) in dataWednesday"
               :key="i"
              >
               {{n.start}} - {{n.end}}
              </div>
            </div>
         </div>
         <div class="BxinfoSchedule">
            <div class="bxlabel">
               <p>Thursday</p>
            </div>
            <div class="bxListTime">
              <div class="times" 
               v-for="(n, i) in dataThursday"
               :key="i"
              >
               {{n.start}} - {{n.end}}
              </div>
            </div>
         </div>
        <div class="BxinfoSchedule">
            <div class="bxlabel">
               <p>Friday</p>
            </div>
            <div class="bxListTime">
              <div class="times" 
               v-for="(n, i) in dataFriday"
               :key="i"
              >
               {{n.start}} - {{n.end}}
              </div>
            </div>
         </div>
         <div class="BxinfoSchedule">
            <div class="bxlabel">
               <p>Saturday</p>
            </div>
            <div class="bxListTime">
              <div class="times" 
               v-for="(n, i) in dataSaturday"
               :key="i"
              >
               {{n.start}} - {{n.end}}
              </div>
            </div>
         </div>
         <div class="BxinfoSchedule">
            <div class="bxlabel">
               <p>Sunday</p>
            </div>
            <div class="bxListTime">
              <div class="times" 
               v-for="(n, i) in dataSunday"
               :key="i"
              >
               {{n.start}} - {{n.end}}
              </div>
            </div>
         </div>
      </div>
      <!-- <div class="bxNote">
         <div class="note">
            <p>Note: You can only have two seperated times per day, do not change weekly times too often, thre are posibilities student already like your time schedules because it fit them perfectly</p>
         </div>
         <div class="bxBtn">
            <v-btn  class="btnSqure">Go to schedule Page</v-btn>
         </div>
      </div>
      <div class="bxBtn">
         <v-btn  class="btnSqure"><router-link to="/coach/coachschedules">Go to schedule Page</router-link></v-btn>
      </div> -->
   </div>
</template>

<script>
      import axios from '../../../../store/axios-auth.js'
      export default {
      data() {
         return {
            dataMonday: null,
            dataTuesday: null,
            dataWednesday: null,
            dataThursday: null,
            dataFriday: null,
            dataSaturday: null,
            dataSunday: null,
         }
      },
      created () {
         const Username = this.$store.state.UpcomingSelect.Username
         axios({
            method: 'get',
            url: `/schedules/${Username}`,
            headers: {Authorization: localStorage.getItem('token')}
         })
         .then(res => {
            this.$store.commit('muttCheckCoachSchedules', res)
            console.log(res);
            // response for each day
            let Monday = res.data.data.filter(x => x.day === 'Monday').map(x => x.Monday)
            this.dataMonday = Monday
            let Tuesday = res.data.data.filter(x => x.day === 'Tuesday').map(x => x.Tuesday)
            this.dataTuesday = Tuesday
            let Wednesday = res.data.data.filter(x => x.day === 'Wednesday').map(x => x.Wednesday)
            this.dataWednesday = Wednesday
            let Thursday = res.data.data.filter(x => x.day === 'Thursday').map(x => x.Thursday)
            this.dataThursday = Thursday
            let Friday = res.data.data.filter(x => x.day === 'Friday').map(x => x.Friday)
            this.dataFriday = Friday
            let Saturday = res.data.data.filter(x => x.day === 'Saturday').map(x => x.Saturday)
            this.dataSaturday = Saturday
            let Sunday = res.data.data.filter(x => x.day === 'Sunday').map(x => x.Sunday)
            this.dataSunday = Sunday
            // response for each day end
            // console.log(this.dataTuesday);
         })
         .catch(function (err) {
            this.$store.commit('muttCheckCoachSchedules', err.response)
            console.log(err);
         });
      }
   }
</script>


