<template>
   <div class="BXviewcoach__BXcard__Container">
      <div class="CoachProfile" >
         <div class="CoachProfile__avatar">
            <v-avatar tile>
               <img
               :src="this.Get_Profile.avatar"
               alt="avatar"
               >
            </v-avatar>
         </div>
         <div class="CoachProfile__name text-capitalize">
            <p>{{this.Get_Profile.fullname}}</p>
         </div>
         <div class="CoachProfile__rating">
            <v-rating half-increments :value= Number(this.Get_Profile.coach_rating) color="yellow darken-3" readonly size="25"></v-rating>
         </div>
         <div class="CoachProfile__timeZone">
            <p class="text-capitalize">{{this.Get_Profile.country}}, {{this.Get_Profile.state}}({{this.Get_Profile.timezone}})</p>
         </div>
         <div class="CoachProfile__Experience">
            <p class="text-capitalize">8 years teachhing experience</p>
         </div>
         <!-- <div class="CoachProfile__ButtonEdit">
            <v-btn  class="btnSqure">Edit Profile</v-btn>
            <v-btn  class="btnSqure">Account Settings</v-btn>
         </div> -->
         <div class="CoachProfile__Bio">
            <blockquote class="blockquote">{{this.Get_Profile.short_bio}}</blockquote>
         </div>
      </div>
      <div class="CoachPersonal">
         <div class="CoachPersonal__Info">
            <div class="CoachPersonal__Info__bxLabel">
               <p>Gender</p>
               <span>:</span>
            </div>
            <div class="CoachPersonal__Info__bxAnswer">
               <p>{{this.Get_Profile.gender}}</p>
            </div>
         </div>
         <div class="CoachPersonal__Info">
            <div class="CoachPersonal__Info__bxLabel">
               <p>English Certification</p>
               <span>:</span>
            </div>
            <div class="CoachPersonal__Info__bxAnswer">
               <p>{{this.Get_Profile.cert_studying}}</p>
            </div>
         </div>
         <div class="CoachPersonal__Info">
            <div class="CoachPersonal__Info__bxLabel">
               <p>Date Joined</p>
               <span>:</span>
            </div>
            <div class="CoachPersonal__Info__bxAnswer">
               <p>{{this.Get_Profile.created_at | dateFormat}}</p>
            </div>
         </div>
         <div class="CoachPersonal__Info">
            <div class="CoachPersonal__Info__bxLabel">
               <p>Spoken Language</p>
               <span>:</span>
            </div>
            <div class="CoachPersonal__Info__bxAnswer">
               <p>{{this.Get_Profile.spoken_language}}</p>
            </div>
         </div>
         <div class="CoachPersonal__Info">
            <div class="CoachPersonal__Info__bxLabel">
               <p>Other Language</p>
               <span>:</span>
            </div>
            <div class="CoachPersonal__Info__bxAnswer">
               <p>{{this.Get_Profile.user_language}}</p>
            </div>
         </div>
      </div>
      <div class="StudentAvatar">
         <div class="BoxStudent">
            <v-avatar size="40">
               <img
                  src="https://cdn.dribbble.com/users/1220170/screenshots/5758802/new_avatar_2x.jpg"
                  alt="avatar"
               >
            </v-avatar>
            <v-avatar size="40">
               <img
                  src="https://66.media.tumblr.com/d52eeaf101248a3ec38926402ba37fe7/tumblr_nmkjoh5oWZ1qzcy5ko1_640.jpg"
                  alt="avatar"
               >
            </v-avatar>
            <v-avatar size="40">
               <img
                  src="https://i.pinimg.com/originals/9c/e2/03/9ce2033132083598eee5c59e58571f82.jpg"
                  alt="avatar"
               >
            </v-avatar>
         </div>
         <div class="total">
            <p>+273 Student has sessions with this Coach</p>
         </div>
         
      </div>
   </div>
</template>

<script>
import moment from 'moment'
export default {
   props:[
      'Get_Profile'
   ],
   filters: {
      dateFormat(value) {
         if (value) {
            return moment(String(value)).format('DD, MMM YYYY')
         }
      },
   },

}
</script>


